@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
  background:  linear-gradient(to right, rgb(17, 85, 141) ,rgb(12, 185, 185) , rgb(107, 248, 150) );
  box-shadow: 0px 4.42184px 107.23px rgba(255, 86, 246, 0.51);
}

.btn:hover {
  background: linear-gradient(
    92.23deg,
    #406aff 21.43%,
    #3bace2 50.63%,
    #b936ee 100%,
    #f3c5f1 117.04%
  );
}

.gradient,
.active {
  background: linear-gradient(to right, rgb(11, 90, 104), rgb(106, 238, 176));
}

.anim{
  color:  linear-gradient(to right, rgb(17, 85, 141) ,rgb(12, 185, 185) , rgb(107, 248, 150) );

}

.active {
  color: #fff;
  padding: 0.7rem;
  border-radius: 100%;
}

.imgg{
  margin-left: -30%
    
}
.imggg{
  /* border: 2px solid black; */
  border-radius: 100%;
  box-shadow: 0px 2px 10px 3px rgba(69, 191, 240, 0.5); 
  filter: brightness(90%);
}

.abt{

  border: none;
 box-shadow: rgba(141, 210, 240, 0.336) 0px 3px 5px 2px, rgba(129, 204, 236, 0.322) 0px 3px 16px 3px;



}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-white;
  }
  .h2 {
    @apply font-primary text-[25px] font-medium tracking-[10%] uppercase ;
  }
  .h3 {
    @apply font-secondary font-semibold text-[20px] mb-6 leading-[29px];
  }
  .btn {
    @apply rounded-full font-primary text-white  font-bold;
  }
  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#43a3df] to-[#52f8a5] font-bold font-primary hover:from-[#FF56F6] hover:to-[#42A6E3];
  }
  .section {
    @apply py-6 lg:py-0 lg:h-screen flex items-center;
  }
  
}
